<template>
  <div class="mainWindow">
    <div class="networkWindow">
      <div class="grid-item-header">bakalarkska praca</div>
      <div class="grid-item-path">cesta</div>
      <HelloWorld
        msg="Welcome to Your Vue.js App"
        class="mojeee grid-item-network"
        @customChange="logChange"
      />
      <Structure class="grid-item-structure" :clickedNodes="clickedNodes"
        >vpravo</Structure
      >
      <div class="grid-item-textarea">
        <div>p</div>
        <footer>
          <span>Tato praca vznikla ako bakalarska praca</span>
        </footer>
      </div>
    </div>
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";
import Structure from "./components/StructureForm.vue";

export default {
  name: "App",
  components: {
    HelloWorld,
    Structure,
  },
  data() {
    return {
      clickedNodes: {
        node1: null,
        node2: null,
      },
    };
  },
  methods: {
    logChange(node) {
      if (this.clickedNodes.node1 !== null) {
        this.clickedNodes.node2 = this.clickedNodes.node1;
        this.clickedNodes.node1 = node;
      } else {
        this.clickedNodes.node1 = node;
      }
    },
  },
};
</script>

